  color: #B8B8B8;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
}
.banner button {
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  margin-top: 60px;
  letter-spacing: 0.8px;
  display: flex;
  align-items: center;
}
.banner button svg {
  font-size: 25px;
  margin-left: 10px;
  transition: 0.3s ease-in-out;
  line-height: 1;
}
.banner button:hover svg {
  margin-left: 25px;
}
.banner img {
  -webkit-animation: updown 3s linear infinite;
          animation: updown 3s linear infinite;
}
@-webkit-keyframes updown {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    50% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
}
@keyframes updown {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    50% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
}
